@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;font-family:'Nunito', sans-serif}body{background-color:rgba(96,146,21,0.15)}.main{display:flex;flex-direction:column;align-items:center}.container{display:flex;align-items:center;margin-bottom:40px}

.button{font-size:22px;padding:5px;background-color:#609215;color:white;border-color:transparent;border-radius:6px;cursor:pointer;transition:0.3s}.button:hover{border-color:#609215;color:#609215;background-color:white}

.wraper-container{min-width:750px;min-height:500px}.charts{width:750px;height:500px}@media (max-width: 780px){.wraper-container{min-width:280px;min-height:200px}.charts{width:280px;height:200px}}

.time-frame__button{font-size:22px;padding:5px;background-color:#609215;color:white;border-color:transparent;cursor:pointer;transition:0.3s}.active{color:#609215;background-color:white}.time-frame{display:flex;margin:0 10px}@media (max-width: 780px){.time-frame__button{padding:3px;font-size:12px}}

.data-error{padding:20px;color:red;font-size:36px;text-align:center;font-weight:700}

.view__currencies-main-data{transition:0.5s;text-shadow:1px 1px #609215;padding:15px;font-size:24px}.view{position:relative;background-color:white;border-radius:8px;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;z-index:2;margin-bottom:20px}.view__contanier{display:flex;align-items:center}.view__initial-state{padding:20px;font-size:36px;text-align:center;font-weight:700}@media (max-width: 780px){.currencies-main-data{padding:5px;font-size:16px}.view__currencies-main-changes{padding-bottom:5px}.view__contanier{flex-direction:column}}

.search-field{width:100px;border-radius:4px;margin:10px;padding:5px;font-size:22px}.search-field__turn-over-button{font-size:32px;background-color:transparent;border:none;cursor:pointer}

.header{width:100%;text-align:center;padding:20px;background-image:url("https://g.foolcdn.com/editorial/images/656662/why-facebook-stock-is-up-meta-platforms-fb.jpg");background-size:cover;background-repeat:no-repeat;margin-bottom:20px}.header__container{display:flex;justify-content:center;align-items:flex-end}.header__logo{margin-left:20px;width:100px}h1{line-height:0}h2{line-height:45px}

